*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #292b33;
}

h2{
  color: white;
}

ul{
  position: relative;
  display: flex;

}

ul li{
  list-style: none;
}

ul li label{
  position: relative;
}

ul li label .icon{
  position: relatve;
  width: 80px;
  height: 80px;
  /* border: 2px solid red; */
  background-color: black;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
  margin: 50px 10px;
  border-radius: 50%;
}

ul li label input{
  position: relative;
  opacity: 0;
}

ul li label input:checked ~ .icon i{
  color: #01f2ff;
  animation: animate 5s linear infinite;
}

@keyframes animate{
  0%{
    transform: rotate(0deg);
  }

  100%{
    transform: rotate(360deg);
  }
}